import React from "react";
import { useParams } from "react-router-dom";
import Tabs from "../Tabs";
import Appointment from "./Appointment";
import Attendence from "./Attendence";
import EmpDocument from "./EmpDocument";
import EmpProfile from "./EmpProfile";
import HRPolicy from "./HRPolicy";
import JobCard from "./JobCard";
import Payroll from "./Payroll";
import PerformanceReport from "./PerformanceReport";

const EmployeeDetails = () => {
  const { id, i } = useParams()
  const data = [
    {
      title: "Profile",
      compo: <EmpProfile clientId={id} />,
      bgcolor: "green",
    },
    {
      title: "Job Profile",
      compo: <JobCard clientId={id} />,
      bgcolor: "green",
    },
    {
      title: "Payroll",
      compo: <Payroll clientId={id} />,
      bgcolor: "green",
    },
    {
      title: "Attendance",
      compo: <Attendence clientId={id} />,
      bgcolor: "green",
    },
    {
      title: "Preformance Report",
      compo: <PerformanceReport clientId={id} />,
      bgcolor: "green",
    },
    {
      title: "Document",
      compo: <EmpDocument clientId={id} />,
      bgcolor: "green"
    },
    {
      title: "Leave Report",
      compo: <Appointment clientId={id} />,
      bgcolor: "green"
    },
    {
      title: "HR Policy",
      compo: <HRPolicy clientId={id} />,
      bgcolor: "green"
    },
  ];

  return (
    <>
      <div className="page_reducer_1">
        <div className="dbox-10">
          <div
            className="text-center"
            style={{ fontWeight: "bold", fontSize: "23px" }}
          >
            Employee Details
          </div>
          <div className="mt-3">
            <Tabs data={data} tabI={i} />
          </div>
        </div>
      </div>
    </>
  );
};

export default EmployeeDetails;
