import React from "react";
import Table from "../Table";

const Attendence = () => {
  const Users = [
    {
      id: 1,
      staff_name: "Sick Leave",
      attendance_id: "5",
      fingerprint: "0",
      active: "ON",
    },
    {
      id: 2,
      staff_name: "Paid Leave",
      attendance_id: "8",
      fingerprint: "0",
      active: "ON",
    },
  ];
  //**Sr No	Emp Name	EMP Mob	EMP ID	Joing date	Gender	Job Post	Department	Location	Full Time Or Part Time	Work From Office or Home	Gross Salary	TWD	Incentive	Profisional Tax	Advance Dedcation	Net take Home	Remark	bank name	ac no	IFSC Code */
  const column = [
    { heading: "S.No", value: "id" },
    { heading: "Date", value: "staff_name" },
    { heading: "Time", value: "staff_name" },
    { heading: "Emp Name", value: "staff_name" },
    { heading: "Shift name", value: "staff_name" },
    { heading: "Shift Timing", value: "staff_name" },
    { heading: "Check in ", value: "staff_name" },
    { heading: "Check out", value: "staff_name" },
  ];

  return (
    <>
      <div>
        <div className="text-center mt-4" style={{ fontSize: "23px" }}>
          Attendance
        </div>

        <Table data={Users} column={column} />
      </div>
    </>
  );
};

export default Attendence;
