import { CButton, CCol, CFormSelect, CRow } from "@coreui/react";
import axios from "axios";
import moment from "moment";
import React, { useEffect } from "react";
import { useState } from "react";
import { Link } from "react-router-dom";
import profile from 'src/assets/images/avatars/profile_icon.png'
import Tabs from "../Tabs";
import IndividualAccess from "./IndividualAccess";
import SubmitAppointment from "./SubmitAppointment";
const url = 'https://yog-seven.vercel.app'

const JobCard = ({ clientId }) => {
    const [action, setAction] = useState("Select");


    let user = JSON.parse(localStorage.getItem('user-info'))
    console.log(user);
    const token = user.token;
    const username = user.user.username;
    const centerCode = user.user.centerCode;

    const [result, setResult] = useState([])
    useEffect(() => {
        getInvoice()
    }, [])
    function getInvoice() {
        axios.get(`${url}/employeeForm/${clientId}`, {
            headers: {
                'Authorization': `Bearer ${token}`
            }
        })
            .then((res) => {
                console.log(res.data)
                setResult(res.data)
            })
            .catch((error) => {
                console.error(error)
            })
    }

    const data = [
        {
            title: "Individual Membership",
            compo: <IndividualAccess clientData={!result} />,
            bgcolor: "#58D68D",
        },
        {
            title: "Multiple Centers Membership",
            compo: "Multiclub Access services",
            bgcolor: "#F39C12",
        },
        {
            title: "Postpaid Membership",
            compo: "Postpaid services",
            bgcolor: "#3498DB",
        },
    ];
    const [openCal, setCal] = useState(false)
    return (
        <>
            <div className="d-flex justify-content-between mt-3 ml-2 mr-2 mb-2">
                <div className="title ml-3"><b>Attendance</b></div>
                <div>
                    <CButton className="btn filter-btn1 " style={{ marginRight: "5px" }}>
                        Inter branch transfer
                    </CButton>
                    <CButton className="btn filter-btn1 " style={{ marginRight: "5px" }}>
                        Print Profile
                    </CButton>
                    <CButton className="btn filter-btn1 " style={{ marginRight: "5px" }}>

                        <Link to={`/forms/invoice/${clientId}`} style={{ color: 'white', textDecoration: 'none' }}>New Invoice</Link>
                    </CButton>
                    <CButton className="btn filter-btn1 " style={{ marginRight: "5px" }}>
                        New Call
                    </CButton>
                    <CButton className="btn filter-btn1 " onClick={() => setCal(!openCal)}>New Appointment</CButton>
                </div>
            </div>
            {openCal &&
                <SubmitAppointment id={clientId} />
            }
            <div className="d-flex justify-content-between mt-5">
                <CRow>
                    <CCol className="mt-2" lg={3} sm={6}>
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="d-flex left ml-3" style={{ gap: "30px" }}>
                                    <img
                                        height="150px"
                                        style={{ borderRadius: "5px" }}
                                        src={profile}
                                        alt="Profile"
                                    />
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-lg-12 mt-2">
                                <div className="d-flex left ml-3" style={{ gap: "30px" }}>
                                    Member Status :
                                    <div
                                        className="d-flex left ml-3"
                                        style={{
                                            gap: "30px",
                                            color: "#fff",
                                            padding: "5px",
                                            borderRadius: "10px",
                                            backgroundColor: "#3498DB",
                                        }}
                                    >
                                        VIP
                                    </div>
                                </div>

                            </div>
                        </div>
                    </CCol>
                    <CCol className="mt-2" lg={3} sm={6}>
                        <CRow>
                            <CCol className="mt-2" lg={12} sm={12}>
                                <div className="d-flex left">Member Id: {String(result?._id).slice(-6)}</div>
                            </CCol>
                            <CCol className="mt-2" lg={12} sm={12}>
                                <div className="d-flex left">Attendance ID: 230</div>
                            </CCol>

                            <CCol className="mt-2" lg={12} sm={12}>
                                <div className="d-flex left">Packeges: 1</div>
                            </CCol>
                            <CCol className="mt-2" lg={12} sm={12}>
                                <div className="d-flex left">Loyalty Points: 0</div>
                            </CCol>
                            <CCol className="mt-2" lg={12} sm={12}>
                                <div className="d-flex left">
                                    <CButton className="btn filter-btn1 ">
                                        Add/View Loyalty Points
                                    </CButton>
                                </div>
                            </CCol>
                        </CRow>
                    </CCol>
                    <CCol className="mt-2" lg={6} sm={12}>
                        <CRow>
                            <CCol className="mt-2" lg={6} sm={6}>
                                <div className="d-flex left">Membership Status</div>
                            </CCol>
                            <CCol className="mt-2" lg={6} sm={6}>
                                <div
                                    className="d-flex left"
                                    style={{ color: "green", fontWeight: "bold" }}
                                >
                                    {result.status}
                                </div>
                            </CCol>
                            <CCol className="mt-2" lg={6} sm={6}>
                                <div className="d-flex left">Start From</div>
                            </CCol>
                            <CCol className="mt-2" lg={6} sm={6}>
                                <div className="d-flex left">{moment(result?.startDate).format("DD-MM-YYYY")}</div>
                            </CCol>
                            <CCol className="mt-2" lg={6} sm={6}>
                                <div className="d-flex left">Packeges Amount: {result?.paidAmount}</div>
                            </CCol>
                            <CCol className="mt-2" lg={6} sm={6}>
                                <div className="d-flex left">Pending Payment: {result?.pendingAmount}</div>
                            </CCol>
                            <CCol className="mt-2" lg={6} sm={6}>
                                <div className="d-flex left">Referrals: 2</div>
                            </CCol>
                            <CCol className="mt-2" lg={6} sm={6}>
                                <div className="d-flex left">Referral value: 0</div>
                            </CCol>
                            <CCol className="mt-2" lg={6} sm={6}>
                                <div className="d-flex left">No of Shop Item: 0</div>
                            </CCol>
                            <CCol className="mt-2" lg={6} sm={6}>
                                <div className="d-flex left">Shop Value: 0</div>
                            </CCol>
                        </CRow>
                    </CCol>
                </CRow>
            </div>

            <div className="d-flex justify-content-between mt-5 ml-2 mr-2">
                <div className="title ml-3">
                    <CButton className="btn filter-btn1 " style={{ marginRight: "5px" }}>
                        Resync To Device
                    </CButton>
                    <CButton className="btn filter-btn1 " style={{ marginRight: "5px" }}>
                        Delete Member
                    </CButton>
                    <div className="btn-group" style={{ marginRight: "5px" }}>
                        <CFormSelect
                            value={action}
                            onChange={(e) => setAction(e.target.value)}
                            options={[
                                "Select",
                                { label: "Device 1", value: "Device 1" },
                            ]}
                        />
                    </div>
                    <CButton className="btn filter-btn1 " style={{ marginRight: "5px" }}>
                        Add Fingerprint
                    </CButton>
                </div>
                <div>
                    <CButton className="btn filter-btn1 " style={{ marginRight: "5px" }}>
                        Inter branch transfer
                    </CButton>
                    <CButton className="btn filter-btn1 " style={{ marginRight: "5px" }}>
                        Print Profile
                    </CButton>
                    <CButton className="btn filter-btn1 " style={{ marginRight: "5px" }}>
                        New Invoice
                    </CButton>
                    <CButton className="btn filter-btn1 " style={{ marginRight: "5px" }}>
                        New Call
                    </CButton>
                    <CButton className="btn filter-btn1 ">New Appointment</CButton>
                </div>
            </div>

            <div className="mt-5 justify-content-center">
                <Tabs data={data} height={"30px"} fontSize={"15px"} />
            </div>
        </>
    );
};

export default JobCard;
